import React, {useState} from 'react';
import s from './GeneralChat.module.css'
import {TextField} from "@mui/material";
import {sendMessage} from "../../redux/chatSlice";
import {useAppDispatch} from "../../redux/store";

export const SendMessageForm= React.memo(({}) => {

    const dispatch = useAppDispatch()

    const [message, setMessage] = useState('')
    const [errorInput, setErrorInput] = useState<null | string>(null)

    const sendMessageChat = () => {
        if (message.trim() === "") {
            setErrorInput("error")
        } else {
            dispatch(sendMessage(message.trim()))
            setMessage('')
        }
    }
    const onKeyPressHandler = (e: React.KeyboardEvent<HTMLInputElement>) => {
        setErrorInput(null)
        if (e.key === "Enter") {
            sendMessageChat()
            e.preventDefault()
        }
    }

    return (
        <TextField className={s.addMessage}
                   margin="dense"
                   multiline
                   maxRows={5}
                   inputProps={{maxLength: 100, style: {fontSize: 12}}}
                   label={'Enter your message'}
                   error={!!errorInput}
                   onKeyDown={onKeyPressHandler}
                   onChange={(e) => setMessage(e.currentTarget.value)}
                   value={message}
                   onBlur={() => setErrorInput(null)}/>
    )
})
