import React, {FC, useEffect, useState} from "react";
import {useGetDialogsQuery, useGetNewMessagesQuery, useStartDialogMutation} from "../../api/dialogs_api";
import {
    Avatar,
    Badge,
    Divider,
    List,
    ListItem,
    ListItemAvatar,
    ListItemButton,
    ListItemText,
    Typography
} from "@mui/material";
import {NavLink, useMatch, useParams, useResolvedPath} from "react-router-dom";
import {LastDate} from "./LastDate";
import MailIcon from "@mui/icons-material/Mail";

type AvatarOfDialogType = {
    large: string | null
    small: string | null
}

type PropsType = {
    dialogId: number
    userName: string
    avatarOfDialog: AvatarOfDialogType
    newMessagesCount: number
    lastUserActivityDate: string
    hasNewMessages: boolean
}

export const Dialogs = React.memo(({dialogId, userName, avatarOfDialog, newMessagesCount, lastUserActivityDate, hasNewMessages}: PropsType) => {

    const userId = Number(useMatch("dialogs/:userId")?.params.userId)
    const [startDialog] = useStartDialogMutation()

    useEffect(() => {
        if(hasNewMessages) {
            startDialog(dialogId)
        }
    }, [dialogId]);
    console.log(userId)

    console.log("Dialogs")

    return (
        <>
                <NavLink to={`${dialogId}`} key={dialogId}>
                    <List sx={{ maxWidth: 360}} component="nav">
                        <ListItem alignItems="flex-start" disablePadding>
                            <ListItemButton
                                sx={{backgroundColor: userId === dialogId ? 'rgba(0, 0, 0, 0.1)' : ''}}>
                                <ListItemAvatar>
                                    <Avatar alt="Remy Sharp" src={avatarOfDialog.small ?? ''} variant="rounded"/>
                                </ListItemAvatar>
                                <ListItemText
                                    primary={userName}
                                    secondary={
                                        <React.Fragment>
                                            <Typography
                                                sx={{display: 'inline'}}
                                                component="span"
                                                variant="body2"
                                            >
                                                  : <LastDate lastDate={lastUserActivityDate}/>
                                            </Typography>
                                        </React.Fragment>
                                    }
                                />
                                <Badge badgeContent={newMessagesCount} color="primary">
                                    <MailIcon color="action"/>
                                </Badge>
                            </ListItemButton>
                        </ListItem>
                        <Divider variant="inset" component="li"/>
                    </List>
                </NavLink>
        </>
    )
})