import React, {useEffect} from "react";
import {clearUserProfile, requestStatus, requestUserProfile} from "../../redux/profileSlice";
import {useParams} from "react-router-dom";
import ProfileStyle from "./Profile.module.css";
import {Profile} from "./Profile/Profile";
import MyPosts from "./MyPosts/MyPosts";
import {useAppDispatch, useAppSelector} from "../../redux/store";
import {Divider, Grid, Paper} from "@mui/material";
import ProfileStatus from "./ProfileStatus/ProfileStatus";

export const ProfilePage = () => {

    const {profile, status} = useAppSelector(state => state.profilePage)
    const authUserId = useAppSelector(state => state.auth.auth.id)

    const dispatch = useAppDispatch()

    let userId: number | null = Number(useParams().userId)

    useEffect(() => {
        if (!userId) {
            userId = authUserId;
        }
        if (userId != null) {
            dispatch(requestUserProfile(userId))
            dispatch(requestStatus(userId))
        }
        return () => {
            dispatch(clearUserProfile())
        }
    }, [userId]);

    return (
        <Grid container item spacing={2} >
            <Grid item xs={12}>
                <Paper sx={{backgroundColor: 'rgba(0, 0, 0, 0.1)', p: 2}} elevation={5}>
                    <ProfileStatus isOwner={!userId} status={status}/>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                <Profile
                    isOwner={!userId}
                    profile={profile}
                    status={status}
                />
            </Grid>
            <Grid item xs={12}>
                <MyPosts />
            </Grid>
        </Grid>
    )
}
