import {ChatMessageAPIType, chatAPI} from "../api/chatAPI";
import {Dispatch} from "redux";
import {v1} from 'uuid'
import {createSlice, PayloadAction} from "@reduxjs/toolkit";

type ChatMessageType = ChatMessageAPIType & {id: string}

export type StatusType = 'pending' | 'ready' | 'error'

const chatSlice = createSlice({
    name: 'chat',
    initialState: {
        messages: [] as ChatMessageType [],
        status: 'pending' as StatusType,
        isOpen: false as boolean
    },
    reducers:{
        messagesReceived(state, action) {
                state.messages = [...state.messages, ...action.payload.messages.map( (m: any) => ({...m, id: v1() }))]
                    .filter((m, index, array) => index >= array.length - 100)
        },
        statusChanged(state, action: PayloadAction<StatusType>) {
            state.status = action.payload
        },
        messagesReset(state) {
            state.messages = []
        },
        setOpenChat(state) {
            state.isOpen = !state.isOpen
        }
    }
})

export const {messagesReceived, statusChanged, messagesReset, setOpenChat} = chatSlice.actions
export default chatSlice.reducer

let _newMessageHandler: ((messages: ChatMessageAPIType[]) => void) | null = null
const newMessageHandler = (dispatch: Dispatch) => {
    if ( _newMessageHandler === null) {
        _newMessageHandler = (messages) => {
            // @ts-ignore
            dispatch(messagesReceived({messages}))
        }
    } return _newMessageHandler
}
let _statusChangedHandler: ((status: StatusType) => void) | null = null
const statusChangedHandler = (dispatch: Dispatch) => {
    if ( _statusChangedHandler === null) {
        _statusChangedHandler = (status) => {
            dispatch(statusChanged(status))
        }
    } return _statusChangedHandler
}

export const startMessagesListening = () => async (dispatch: any) => {
    chatAPI.start()
    chatAPI.subscribe('messages-received',  newMessageHandler(dispatch))
    chatAPI.subscribe('status-changed',  statusChangedHandler(dispatch))
}

export const stopMessagesListening = () => async (dispatch: any) => {
    dispatch(messagesReset())
    chatAPI.unsubscribe('messages-received', newMessageHandler(dispatch))
    chatAPI.unsubscribe('status-changed', statusChangedHandler(dispatch))
    chatAPI.stop()
}

export const sendMessage = (message: string) => async () => {
    chatAPI.sendMessage(message)
}
