import React, {useEffect, useState} from "react";
import {Typography} from "@mui/material";

type PropsType = {
    lastDate: string
}

export const LastDate = ({lastDate}: PropsType) => {

    const [lastMessageTime, setLastMessageTime] = useState('')
    const [lastMessageDate, setLastMessageDate] = useState('')

    useEffect(() => {
        const convertDate = (date: string) => {
            const newDate = date.split('T')
            let lastTime = `${Number(newDate[1].slice(0, 2)) + 3}${newDate[1].slice(2,5)}`
            setLastMessageTime(lastTime)
            let lastDate = newDate[0].slice(2)
            setLastMessageDate(lastDate)
        }
        convertDate(lastDate)
    }, []);

    return (
        <>
            <Typography variant="subtitle2" component="span">
            {lastMessageDate} {lastMessageTime}
            </Typography>
        </>
    )
}