import React from "react";
import {logIn} from "../../redux/authSlice";
import {Navigate} from "react-router-dom";
import {LoginForm} from "./LoginForm";
import {useAppDispatch, useAppSelector} from "../../redux/store";

export const Login = () => {

    const isAuth = useAppSelector(state => state.auth.auth.isAuth)
    const captcha = useAppSelector(state => state.auth.captcha)

    const dispatch = useAppDispatch()

    const login = (email: string, password: string, rememberMe: boolean, captcha: string | null) => {
        dispatch(logIn(email, password, rememberMe, captcha))
    }

    if(isAuth) {
        return <Navigate to={"/"} />
    }

    return (
        <div >
           <LoginForm  login={login} captcha={captcha}/>
        </div>
    )
}
