import React, {useState} from "react";
import style from "./Dialog.module.css"
import {
    useDeleteMessageMutation,
    useGetMessagesQuery,
    useSendMessageMutation,
    useStartDialogMutation
} from "../../api/dialogs_api";
import {useParams} from "react-router-dom";
import {
    Box,
    Button,
    ButtonBase,
    Container,
    Grid,
    IconButton,
    Pagination,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import {LastDate} from "./LastDate";
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import {AvatarForMessage} from "./AvatarForMessage";
import {Stack} from "@mui/system";

type PropsType = {}

export const Dialog = ({}: PropsType) => {

    const dialogUserId = Number(useParams().userId)
    const [currentPage, setCurrentPage] = useState(1)

    const [sendMessage] = useSendMessageMutation()

    const [deleteMessage] = useDeleteMessageMutation()
    const {data, isSuccess} = useGetMessagesQuery({userId: dialogUserId, page: currentPage})
    const totalPage = isSuccess ? Math.ceil(data.totalCount / 10) : 0;

    console.log("Dialog")

    const [message, setMessage] = useState<string>('')

    const sendMessageHandle = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        sendMessage({userId: dialogUserId, body: message})
        setMessage('')
    }

    const deleteMessageHandle = (messageId: string) => {
        deleteMessage(messageId)
    }

    const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
        setCurrentPage(value);
    };

    return (
        <Container>
            <Paper sx={{backgroundColor: 'rgba(0, 0, 0, 0.1)', padding: 2}}>
                <Grid container sx={{p: 2}}>
                    <Grid container rowSpacing={2} sx={{height: 600, overflowY: "scroll", display: 'block'}}
                          className={style.dialogWindow}>
                        {isSuccess && data.items.map(message =>
                            <Grid item key={message.id}>
                                {/*<Paper*/}
                                {/*    sx={{*/}
                                {/*        p: 1,*/}
                                {/*        margin: 'auto',*/}
                                {/*        maxWidth: 500,*/}
                                {/*        flexGrow: 1,*/}
                                {/*        backgroundColor: 'rgba(0, 0, 0, 0.1)',*/}
                                {/*        */}
                                {/*    }}>*/}
                                    <Grid container spacing={2}>
                                        <Grid item xs={12} container rowSpacing={1}>
                                            <Grid item xs container columnSpacing={2}>
                                                <Grid item>
                                                    <ButtonBase>
                                                        <AvatarForMessage senderId={message.senderId}/>
                                                    </ButtonBase>
                                                </Grid>
                                                <Grid item xs>
                                                    <Typography variant="subtitle1">
                                                        {message.senderName}
                                                    </Typography>
                                                </Grid>
                                                <Grid item>
                                                    <Typography variant="subtitle1" component="div">
                                                        <LastDate lastDate={message.addedAt}/>
                                                        <IconButton onClick={() => deleteMessageHandle(message.id)}>
                                                            <DeleteForeverIcon/>
                                                        </IconButton>
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                            <Grid item container>
                                                {/*<Grid item xs sx={{p: 1}}>*/}
                                                    <Paper sx={{p: 1, backgroundColor: 'rgba(0, 0, 0, 0.1)', width: '95%'}}>
                                                    <Typography variant="body2"
                                                                sx={{wordWrap: 'break-word'}}>
                                                        {message.body}
                                                    </Typography>
                                                    </Paper>
                                                {/*</Grid>*/}
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                {/*</Paper>*/}
                            </Grid>
                        )}
                    </Grid>
                    <Stack spacing={2} sx={{marginX: 'auto', marginTop: 1}}>
                        <Pagination count={totalPage} page={currentPage} size="small"
                                    onChange={handleChange} variant="outlined" color="primary"/>
                    </Stack>
                    <Box
                        component="form"
                        sx={{width: '100%', marginTop: 2}}
                        noValidate
                        autoComplete="off"
                    >
                        <TextField
                            fullWidth
                            id="outlined-multiline-flexible"
                            label="enter your message"
                            multiline
                            maxRows={4}
                            value={message}
                            onChange={e => setMessage(e.target.value)}
                        />
                    </Box>
                    <Button variant="contained" onClick={sendMessageHandle} sx={{marginTop: 1}}>Send</Button>
                </Grid>
            </Paper>
        </Container>
    )
}
