import React, {useState} from "react";
import {deletePost} from "../../../../redux/profileSlice";
import {useDispatch} from "react-redux";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import {Avatar, Box, Button, Grid, IconButton, Paper, Typography} from "@mui/material";
import {FavoriteBorderOutlined} from '@mui/icons-material';

type PropsTypes = {
    id: number
    message: string
    like: number
    date: string
}

const Post = ({id, message, like, date}: PropsTypes) => {

    const [likeCount, setLikeCount] = useState(like)

    const dispatch = useDispatch()

    return (
        <Grid item xs={12}>
            <Paper sx={{backgroundColor: 'rgba(0, 0, 0, 0.1)', p: 2}} elevation={5}>
                <Grid container spacing={2}>
                    <Grid item container>
                        <Grid item container direction='row' xs spacing={2} alignItems="center">
                            <Grid item>
                                <Avatar alt='avatar' src='sd'/>
                            </Grid>
                            <Grid item>
                                <Typography variant="subtitle1" component="div">
                                    Dmitriy
                                </Typography>
                                {date}
                            </Grid>
                        </Grid>
                        <Grid item xs container justifyContent="flex-end">
                            <Grid item>
                                <IconButton onClick={() => dispatch(deletePost(id))} >
                                    <DeleteForeverIcon/>
                                </IconButton>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} container justifyContent='flex-end'>
                        <Box sx={{width: '93%'}}>
                            <Typography variant="body2" component='span' sx={{wordWrap: 'break-word'}}>
                                {message}
                            </Typography>
                        </Box>
                    </Grid>
                    <Grid item xs={12}>
                        <IconButton onClick={() => setLikeCount(like => like + 1)}>
                            <FavoriteBorderOutlined/>
                        </IconButton>
                        {likeCount}
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    )
}

export default Post;

