import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {PhotosType} from "../redux/types/types";
import {number} from "prop-types";
import {ResultType} from "@remix-run/router/utils";


type UserDialogType = {
    id: number
    hasNewMessages: boolean
    lastDialogActivityDate: string
    lastUserActivityDate: string
    userName: string
    photos: {
        large: string | null
        small: string | null
    }
    newMessagesCount: number
}

export type ResponseDialogsType = UserDialogType[]

export type UserMessageType = {
    addedAt: string
    id: string
    body: string
    recipientId: number
    senderId: number
    senderName: string
    viewed: boolean
}

export type UserMessagesType = {
    error: null | string
    items: UserMessageType[]
    totalCount: number
}

type GetUserType = {
    photos: {
        small: string
    }
}

export const dialogsApi = createApi({
    reducerPath: 'dialogsAPI',
    baseQuery: fetchBaseQuery({
        baseUrl: 'https://social-network.samuraijs.com/api/1.0/',
        credentials: "include",
        headers: {
            "API-KEY": "6a48818c-31c7-405a-9c16-8b1dcccca3c7"
        }}),
    tagTypes: ['dialogs', 'messages'],
    endpoints: (builder) => ({
        getDialogs: builder.query<ResponseDialogsType, unknown>({
            query: () => ({
                url: `dialogs`,
            }),
            providesTags: ['dialogs']
        }),
        startDialog: builder.mutation({
            query: ({userId}) => ({
                url: `dialogs/${userId}`,
                method: 'PUT',
            }),
            invalidatesTags: ["dialogs"]
        }),
        getMessages: builder.query<UserMessagesType, {userId: number, page: number }>({
            query: ({userId, page}) => ({
                url: `dialogs/${userId}/messages`,
                params: {count: 10, page: page}
            }),
            // merge(currentCacheData, responseData) {
            //
            //         let cacheData = currentCacheData.items
            //         cacheData = [...responseData.items, ...cacheData]
            //
            //     // currentCacheData.items.push(...responseData.items.slice(-1))
            // },
            providesTags: ['messages']

        }),
        getAuthUser: builder.query<GetUserType, number>({
            query: (userId) => ({
                url: `/profile/${userId}`,
            }),
            // providesTags: ['messages']
        }),
        sendMessage: builder.mutation({
            query: ({userId, body}) => ({
                url: `dialogs/${userId}/messages`,
                method: 'POST',
                body: {body}
            }),

            invalidatesTags: ['messages']
        }),
        deleteMessage: builder.mutation({
            query: (messageId) => ({
                url: `dialogs/messages/${messageId}`,
                method: 'DELETE',
            }),
            invalidatesTags: ['messages']
        }),
        getNewMessages: builder.query<UserMessagesType, unknown>({
            query: () => ({
                url: `dialogs/messages/new/count`,
            }),
            // providesTags: ['messages']
        }),

    })
})


export const {useSendMessageMutation, useStartDialogMutation, useGetMessagesQuery,
    useGetDialogsQuery, useDeleteMessageMutation, useGetNewMessagesQuery, useGetAuthUserQuery} = dialogsApi;