import React, {useState} from "react";
import style from "./GeneralChat.module.css";
import {useAppDispatch, useAppSelector} from "../../redux/store";
import {Messages} from "./Messages";
import {SendMessageForm} from "./SendMessageForm";
import {Button, Grid, Paper} from "@mui/material";
import Draggable from 'react-draggable';
import {setOpenChat} from "../../redux/chatSlice";

export const ChatWindow = () => {

    const dispatch = useAppDispatch()

    const {status, isOpen} = useAppSelector(state => state.chat)
    const isAuth = useAppSelector(state => state.auth.auth.isAuth)

    const [chat, setChat] = useState<boolean>(false)

    const [offsetX, setOffsetX] = useState<number>(0)
    const [offsetY, setOffsetY] = useState<number>(0)
    const [x, setX] = useState<number>(300);
    const [y, setY] = useState<number>(300);
    const [isDrag, setIsDrag] = useState(false)


    function dragStartHandler(e: React.DragEvent<HTMLDivElement>) {
        setOffsetX(e.clientX - x)
        setOffsetY(e.clientY - y)
    }

    function dragEndHandler(e: any) {

    }

    function dragOverHandler(e: any) {

        e.preventDefault()
    }

    function dropHandler(e: React.DragEvent<HTMLDivElement>) {
        e.preventDefault()
    }

    function dragHandler(e: any) {
        setX(e.clientX - offsetX);
        setY(e.clientY - offsetY)
    }

    return (
        <>
            {isAuth
                &&

                    <Grid style={{left: x, top: y, position: 'absolute', zIndex: '999'}}
                          onDrag={dragHandler}
                          onDragStart={dragStartHandler}
                          onDragEnd={dragEndHandler}
                          onDragOver={dragOverHandler}
                          onDrop={dropHandler}
                          draggable={true}>
                        {status === 'error' && <div>Some error occurred. Please refresh the page.</div>}
                        {isOpen
                            && <Paper sx={{width: 250, p: 1, backgroundColor: 'rgb(135, 136, 140)'}}>
                                <Messages chat={chat}/>
                                <SendMessageForm/>
                                <div className={style.closeChat} >
                                    <button onClick={() => dispatch(setOpenChat())}>Close chat</button>
                                </div>
                            </Paper>
                        }
                    </Grid>

                }
        </>
    )
}
