import React from "react";
import {Route, Routes} from "react-router-dom";
import {Dialog} from "./Dialog";
import {Container, Grid} from "@mui/material";
import {Dialogs} from "./Dialogs";
import {useGetDialogsQuery} from "../../api/dialogs_api";

export const DialogsPage = React.memo(() => {

    const {data: dialogs, isSuccess} = useGetDialogsQuery([])

    if(isSuccess) {
        console.log(dialogs)
    }

    console.log("DialogsPage")

    return (

            <Grid container >
                <Grid item xs={4}>
                    {isSuccess && dialogs.map(dialog =>
                        <Dialogs
                            key={dialog.id}
                            dialogId={dialog.id}
                            userName={dialog.userName}
                            avatarOfDialog={dialog.photos}
                            newMessagesCount={dialog.newMessagesCount}
                            lastUserActivityDate={dialog.lastUserActivityDate}
                            hasNewMessages={dialog.hasNewMessages}
                        />)}
                    {/*<Dialogs />*/}
                </Grid>
                <Grid item xs={8}>
                    <Routes>
                        <Route path="/:userId/" element={<Dialog/>}/>
                        <Route path="/" element={<div>select a dialog</div>}/>
                    </Routes>
                </Grid>
            </Grid>

    )
})