import React, {lazy, Suspense, useEffect} from "react";
import './App.css';
import {Route, Routes} from "react-router-dom";
import {Header} from "./components/Header/Header";
import {initializeApp} from "./redux/appSlice";
import Preloader from "./components/common/preloader/preloader";
import {useAppDispatch, useAppSelector} from "./redux/store";
import {Login} from "./components/Login/Login";
import {ProfilePage} from "./components/Profile/ProfilePage";
import {ChatWindow} from "./components/GeneralChat/ChatWindow";
// import {UsersPage} from "./components/Users/UsersPage";
import {DialogsPage} from "./components/Dialogs/DialogsPage";
import {Navbar} from "./components/Navbar/Navbar";
import {Box, Container, Grid, LinearProgress} from "@mui/material";
// import {UsersPage} from "./components/Users/UsersPage";

const UsersPage = lazy(() => import('./components/Users/UsersPage')
    .then(promise => ({default: promise.UsersPage})));

export const App = React.memo(() => {

    console.log("App")

    const initialize = useAppSelector(state => state.app.initialized)

    const dispatch = useAppDispatch()

    useEffect(() => {
        dispatch(initializeApp())
    }, []);

    if (!initialize) {
        return <Box sx={{width: '100%'}}>
            <LinearProgress/>
        </Box>
    }

    return (
        <Box>
            <Header/>
            <Container sx={{width: 1100, mt: 2}}>
                <Grid container spacing={3}>
                    <Grid item xs={2}>
                        <Navbar/>
                        <ChatWindow/>
                    </Grid>
                    <Grid item xs={10}>
                        <Suspense>
                            <Routes>
                                <Route path="/:userId?" element={<ProfilePage/>}/>
                                <Route path="/*" element={<ProfilePage/>}/>
                                <Route path="/login/*" element={<Login/>}/>
                                <Route path="/users/*" element={<UsersPage/>}/>
                                <Route path="/test/" element={<UsersPage/>}/>
                                {/*<Route path="/dialogs/" element={<Dialogs/>}/>*/}
                                <Route path="/dialogs/*" element={<DialogsPage/>}/>
                            </Routes>
                        </Suspense>
                    </Grid>
                </Grid>
            </Container>
        </Box>
    );
});