import React, {useEffect, useRef, useState} from 'react';
import s from './GeneralChat.module.css'
import avatar from "../../assets/images/avatar.svg";
import {useAppDispatch, useAppSelector} from "../../redux/store";
import {startMessagesListening, stopMessagesListening} from "../../redux/chatSlice";

type PropsType = {
    chat: boolean
}

export const Messages = React.memo(({chat}: PropsType) => {

    const {messages, isOpen} = useAppSelector(state => state.chat)
    const authUserId = useAppSelector(state => state.auth.auth.id)

    const dispatch = useAppDispatch()

    useEffect(() => {
        if (isOpen) {
            dispatch(startMessagesListening())
            return () => {
                dispatch(stopMessagesListening())
            }
        }
    }, [isOpen]);

    const [autoScroll, setAutoScroll] = useState(true)
    const scrollHandler = (e: React.UIEvent<HTMLDivElement, UIEvent>) => {
        const element = e.currentTarget
        if (Math.abs((element.scrollHeight - element.scrollTop) - element.clientHeight) < 200) {
            !autoScroll && setAutoScroll(true)
        } else {
            autoScroll && setAutoScroll(false)
        }
    }

    const messagesBlockRef = useRef<HTMLDivElement>(null)

    useEffect(() => {
        if (autoScroll) messagesBlockRef.current?.scrollIntoView(true)

    }, [messages]);

    return (
        <div className={s.messageChat} onScroll={scrollHandler}>
            {messages.map(message =>
                <div className={message.userId !== authUserId ? s.messageItem : s.messageItemAuth}
                     key={message.id}>
                    <div className={s.photo}>
                        <img src={message.photo ?? avatar} alt={''}/>
                    </div>
                    <div>
                        <div className={s.userName}> {message.userName}</div>
                        <div className={s.message}>{message.message}</div>
                    </div>
                </div>
            )}
            <div ref={messagesBlockRef}></div>
        </div>
    )
})
