import React from "react";
import {useGetAuthUserQuery} from "../../api/dialogs_api";
import {Avatar} from "@mui/material";

type PropsType = {
    senderId: number
}

export const AvatarForMessage = ({senderId}: PropsType) => {

    const {data, isSuccess} = useGetAuthUserQuery(senderId);

    return (
        <>
            <Avatar alt="" src={ isSuccess ? data.photos.small : ''} variant="rounded"/>
        </>
    )
}